$(document).ready(function() {
	var imgID = null;
	$(".img-container").click(function() {
			$("body").css("overflow-y", "hidden");
		$(".overlay").show();
		$(".item-info-modal").show();
	});

	$(".item-info-modal__close-link").click(function () {
		$(".overlay").hide();
		$("body").css("overflow-y", "initial");
	});
});